













import Vue from 'vue';
import { Component, Watch, Prop } from 'vue-property-decorator';
import { TweenMax } from 'gsap';

@Component({
    components: {
        
    },
})
export default class ProfileTab extends Vue {

    @Prop({default: ''})
    tLabel;

    @Prop({default: ''})
    value;

    @Prop({default: true})
    left;

    @Prop({default: true})
    good;

    get backgroundColor(): string{
        if(this.good){
            return "#ADC4A0";
        }else{
            return "#C4A0A0";
        }
    }

    get labelColor(): string{
        if(this.good){
            return "#55897D";
        }else{
            return "#895555";
        }
    }

    get valueColor(): string{
        if(this.good){
            return "#87997D";
        }else{
            return "#997D7D";
        }
    }

}

